
  import Picker from 'vux/src/components/picker/index.vue'


  export default {
    name: 'bottomBar',
    data() {
      return {
        page: this.currentPage,
        isShowPicker: false,
      };
    },
    props: {
      currentPage: {type: Number},
      allPage: {type: Number},
      recommend: {type: Number}
    },
    computed: {
      pageItem() {
        return ['第' + this.page + '页']
      },
      pageList() {
        let pageList = [[]]
        for (let i = 1; i <= this.allPage; i++) {
          pageList[0].push('第' + i + '页')
        }
        return pageList
      },
      pageItem() {
        return ['第' + this.page + '页']
      }
    },
    components: {Picker},
    methods: {
      change(value) {
        this.page = Number(value[0].replace(/[^\d]/g, ''))
      },
      gotoPage(value) {
        this.page = value;
//        this.$emit('gotoPage',value)
        this.isShowPicker = false;
        window.postMessage('page' + value);
      },
      togglePicker() {
        this.isShowPicker = !this.isShowPicker
      },
    },
    filters: {
      getNumber(value){
        if (!value) return ''
        value = value.replace(/[^\d]/g, '')
        return value
      }
    }
  };



