  import data from '../data/thread'
  import ThreadContent from '../components/threadContent'
  import Sticky from '../components/sticky'
  import Reply from '../components/reply'
  import BottomBar from '../components/bottomBar'

  import $ from 'n-zepto'
  export default {
    name: 'app',
    components: {
      ThreadContent, Sticky, Reply, BottomBar
    },
    mounted() {
      let that = this

//      console.log(data)
//      this.thread = data[0].offline_data.data;
//      this.lightReply = data[2].list;
//      this.allReply = data[1].result.list
//      this.allPage = data[1].result.all_page;


      document.addEventListener('message', function (e) {
        let data = JSON.parse(e.data)
        switch (data.type) {
          case 'threads':
//            that.test = '234'
            that.thread = data.res.offline_data.data;
            window.postMessage('threadOK');
            break;
          case 'reply':
            that.allReply = data.res.result.list;
            that.allPage = data.res.result.all_page;
            that.currentPage = data.page
            // 回到顶部
            document.documentElement.scrollTop = document.body.scrollTop = 0;
            break;
          case 'light':
            that.lightReply = data.res.list;
            break
        }
      });
    },
    methods: {
//      gotoPage(page){
//        console.log(page)
//        window.postMessage('page' + page);
//      }
    },
    data() {
      return {
//        test: '123',
        thread: {},
        lightReply: [],
        allReply: [],
        currentPage: 1,
        allPage: 1
      }
    }
  }



