  export default {
    name: 'sticky',
    data() {
      return {
        isSticky: false
      };
    },
    props: {
      lightReply: {type: Array}
    },
    components: {},
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    methods: {
      handleScroll(e) {
        let $sticky = $(this.$refs.sticky)
//        console.log($sticky[0].offsetTop)
//        console.log(window.scrollY)
        if (window.scrollY >= $sticky[0].offsetTop) {
          this.isSticky = true
        } else {
          this.isSticky = false
        }
      }
    }
  };



